<template>
  <div class="game">
    <a-scene
      vr-mode-ui="enabled: false"
    >
      <a-assets ref="assets">
        <a-asset-item id="virus1" src="static_assets/models/Virus01.glb"></a-asset-item>
      </a-assets>

      <!-- Player Bullet -->
      <a-entity
        bullet="name: player; speed: 30.0; poolSize: 20"
        geometry="primitive: icosahedron; radius: 0.1;"
        material="color: #000; emissiveIntensity: 0.5; emissive: #000">
      </a-entity>

      <!-- Enemy Bullet -->
      <a-entity
        bullet="name: enemy; speed: 10.0; poolSize: 20"
        geometry="primitive: icosahedron; radius: 0.1;"
        material="color: #ff0000; emissiveIntensity: 0.5; emissive: #ff0000">
      </a-entity>

      <!-- Enemies -->
        <!-- target="static: false" -->
      <a-entity
        id="enemy1"
        class="enemies"
        animation-mixer
        enemy="assetId: virus1;"
        random-spawn="min: -5 -1 -5;
                      max: 5 2 5;
                      radius: 2"
        visible="false"
        animation="property: position;
                  to: 0 1.6 0;
                  dur: 20000;
                  easing: easeInOutCubic;
                  startEvents: spawn;
                  pauseEvents: despawn"
        proxy-event__spawn="event: spawn; to: CHILDREN"
        proxy-event__despawn="event: despawn; to: CHILDREN"
        target="static: false"
        camera
        look-at="#player"
      >
        <a-entity
          shooter="activeBulletType: enemy"
          enemy-shooter
        >
        </a-entity>
      </a-entity>

      <!-- Player -->
        <a-camera
          id="player"
          active="true"
          target
          sphere-collider="objects: .enemies; radius: 0.1"
          geometry="primitive: sphere; radius: 0.1"
          position="0 1.6 0"
          shooter="activeBulletType: player"
          player
          player-shooter="ammo: 10"
        >
        </a-camera>
        <!-- <a-entity
          geometry="primitive: sphere; radius: 0.1"
          visible="true"
        >
        </a-entity> -->
    </a-scene>

    <component :is="hudComponent"></component>
  </div>
</template>

<script>
import 'aframe';
import 'aframe-super-shooter-kit';
import 'aframe-extras';
import 'aframe-proxy-event-component';
import 'aframe-look-at-component';

// Aframe Components
import './components/player';
import './components/playerShooter';
import './components/enemy';
import './components/enemyShooter';
import './components/randomSpawn';

// Vue Components
import Hud from '@/components/Hud/Hud.vue';

// Utils
import camera from './utils/camera';

export default {
  name: 'Game',
  components: {
    Hud,
  },
  data() {
    return {
      hudComponent: undefined,
    };
  },
  mounted() {
    const { assets } = this.$refs;

    assets.addEventListener('loaded', () => {
      this.hudComponent = Hud;
      camera.init();
    });
  },
};
</script>
