import AFRAME from 'aframe';
import store from '@/store';

AFRAME.registerComponent('enemy-shooter', {
  schema: {
    playerPos: { type: 'vec3', default: { x: 0, y: 1.6, z: 0 } },
    shootingDelay: { type: 'number', default: 5000 },
    shootingDistance: { type: 'number', default: 5 },
  },
  init() {
    const { el } = this;

    this.onSpawn = this.onSpawn.bind(this);
    el.addEventListener('spawn', this.onSpawn);

    this.onDespawn = this.onDespawn.bind(this);
    el.addEventListener('despawn', this.onDespawn);
  },
  moveCameraForward(el) {
    el.object3D.translateZ(-1.0);
  },
  onSpawn() {
    const { el } = this;

    this.moveCameraForward(el);
    el.addState('spawned');
  },
  onDespawn() {
    const { el } = this;

    clearInterval(this.shootInterval);
    el.removeState('spawned');
    el.removeState('shooting');
  },
  tick() {
    if (this.el.is('spawned')) {
      const distance = this.el.object3D.position.distanceTo(this.data.playerPos);

      if (distance < this.data.shootingDistance) this.startShooting();
    }
  },
  startShooting() {
    if (!this.el.is('shooting')) {
      console.log('atirou');
      this.shoot();
      this.el.addState('shooting');
    }
  },
  shoot() {
    const { el, data } = this;

    this.shootInterval = setInterval(() => {
      const isPlaying = store.getters['game/isPlaying'];

      if (!isPlaying) return;

      el.emit('shoot');
    }, data.shootingDelay);
  },
  remove() {
    clearInterval(this.shootInterval);
  },
});
